import {PolyDictionary} from './../../../../../../../@polygonjs/polygonjs/src/types/GlobalTypes';
import {Ref, ref, computed} from 'vue';

export enum MarketplacePolyNodeTab {
	MY_POLY_NODES = 'My Poly Nodes',
	BROWSE = 'Browse Marketplace',
}
export const MARKETPLACE_POLY_NODE_TABS: MarketplacePolyNodeTab[] = [
	MarketplacePolyNodeTab.MY_POLY_NODES,
	MarketplacePolyNodeTab.BROWSE,
];
const MARKETPLACE_HTML_ELEMENT: PolyDictionary<string> = {
	[MarketplacePolyNodeTab.MY_POLY_NODES]:
		'<span>My PolyNodes</span><span class="relative -top-2 ml-1 inline-flex items-center rounded-full bg-yellow-100 px-1.5 py-px text-xs font-small text-yellow-800">Beta</span>',
	[MarketplacePolyNodeTab.BROWSE]: '<span>Browse Marketplace</span>',
};

export function SetupTabs() {
	// tabs
	const currentTabIndex: Ref<number> = ref(MARKETPLACE_POLY_NODE_TABS.indexOf(MarketplacePolyNodeTab.MY_POLY_NODES));
	function selectTab(index: number) {
		currentTabIndex.value = index;
	}
	const currentTabName = computed(() => {
		return MARKETPLACE_POLY_NODE_TABS[currentTabIndex.value];
	});
	const isCurrentTabMyPolyNodes = computed(() => currentTabName.value == MarketplacePolyNodeTab.MY_POLY_NODES);
	const isCurrentTabBrowse = computed(() => currentTabName.value == MarketplacePolyNodeTab.BROWSE);
	const tabActiveStates = computed(() => MARKETPLACE_POLY_NODE_TABS.map((tabName, i) => currentTabIndex.value == i));
	const tabHTMLElements = computed(() => {
		return MARKETPLACE_POLY_NODE_TABS.map((tabName) => MARKETPLACE_HTML_ELEMENT[tabName]);
	});
	// const tabClassObjects = computed(() => {
	// 	return MARKETPLACE_POLY_NODE_TABS.map((tabName, i) => {
	// 		return {
	// 			active: tabActiveStates.value[i],
	// 		};
	// 	});
	// });
	// --

	return {
		currentTabIndex,
		MARKETPLACE_POLY_NODE_TABS,
		selectTab,
		currentTabName,
		isCurrentTabMyPolyNodes,
		isCurrentTabBrowse,
		// tabClassObjects,
		tabActiveStates,
		tabHTMLElements,
	};
}
