import {CoreType} from './../../../../../../../@polygonjs/polygonjs/src/core/Type';
import {Ref, watch} from 'vue';
interface LocalStorageOptions {
	currentTabIndex: Ref<number>;
}

const KEY = 'marketplace-poly-nodes-currentTab';

export function SetupLocalStorage(options: LocalStorageOptions) {
	function saveToLocalStorage() {
		localStorage.setItem(KEY, `${options.currentTabIndex.value}`);
	}

	watch(options.currentTabIndex, saveToLocalStorage);

	function loadFromLocalStorage() {
		return localStorage.getItem(KEY);
	}

	const current = loadFromLocalStorage();
	if (current && CoreType.isNumber(parseInt(current))) {
		options.currentTabIndex.value = parseInt(current);
	}

	return {
		saveToLocalStorage,
		loadFromLocalStorage,
	};
}
